import React, {useState} from 'react';
import {Form, Header, Icon, Message, Radio, Rating} from "semantic-ui-react";
import {Button} from "../utils/UI";
import {Field, formValueSelector, reduxForm, SubmissionError} from "redux-form";
import CustomModal from "../utils/Modal";
import formComponentGenerator, {
    formInputComponent,
    formRadioComponent, formRatingComponent,
    formTextComponent
} from "../utils/ReduxFormComponentWrapper";
import {connect} from "react-redux";
import {submitFeedback} from "../../store/actions";

const messageTypes = [
    {
        name: 'review',
        text: 'Написать отзыв'
    },
    {
        name: 'question',
        text: 'Задать вопрос'
    },
    {
        name: 'critical',
        text: 'Сообщить о критической проблеме'
    },
    {
        name: 'warning',
        text: 'Предупредить о небольшой ошибке'
    },
    {
        name: 'idea',
        text: 'Предложить идею / интересную фичу / новый функционал'
    },
    {
        name: 'offer',
        text: 'Сотрудничество / работа'
    },
];


const submitValidate = (values) => {
    let error = null;
    if (!values.messageType)
        error = 'Выберите тип обращения';

    if (values.messageType === 'question' && !values.contact)
        error = 'Укажите контакт, чтобы мы могли вам ответить';

    if (values.messageType === 'offer' && !values.contact)
        error = 'Укажите контакт, чтобы мы смогли с вами связаться';

    if (!values.message)
        error = 'Поле с сообщением не заполнено';

    if (error)
        throw new SubmissionError({
            _error: error
        });
};


const AlertModal = (props) => {
    const {handleClose, header, text} = props;

    return (
        <CustomModal handleClose={handleClose}>
            <Header>{header}</Header>
            <div style={{margin: '20px'}}>
                <p style={{fontSize: '1.2rem'}}>{text}</p>
                <Button basic style={{float: 'right', marginBottom: '10px'}} text={'Закрыть'} onClick={handleClose}/>
            </div>
        </CustomModal>
    )
};


const Feedback = (props) => {
    let {handleSubmit, handleClose} = props;
    console.log('props', props);
    const [visibleAlert, setVisibleAlert] = useState(false);
    const [successMessage, setSuccessMessage] = useState('');

    const onSubmit = (values) => {
        submitValidate(values);
        setVisibleAlert(true);
        let message = 'Спасибо за обращение!';

        if (values.messageType === 'question')
            message = 'Мы постараемся ответить в ближайшее время!';
        if (values.messageType === 'review')
            message = 'Спасибо за отзыв!';
        if (values.messageType === 'warning')
            message = 'Постараемся исправить баг как можно быстрее!';
        if (values.messageType === 'critical')
            message = 'Вся команда уже взялась за исправление ошибки!';
        if (values.messageType === 'offer')
            message = 'Мы с вами свяжемся в ближайшее время!';
        if (values.messageType === 'idea')
            message = 'Спасибо за ваше предложение. Самые лучшие идеи будут воплащены в ближайшее время!';

        props.onSubmit(values);
        setSuccessMessage(message);
    };

    handleSubmit = handleSubmit(onSubmit);
    return (
        <>
            {
                visibleAlert ?
                    <AlertModal handleClose={handleClose}
                                header={'Сообщение отправлено'}
                                text={successMessage}/> :
                    <CustomModal handleClose={handleClose}>
                        <Header>Написать разработчикам</Header>
                        {
                            props.error &&
                            <Message
                                style={{margin: '10px'}}
                                error
                            >{props.error}</Message>
                        }
                        <div style={{margin: '30px'}}>
                            <Form>
                                <Form.Field>
                                    <label>Цель обращения</label>
                                </Form.Field>
                                {
                                    messageTypes.map(messType => (
                                        <Field
                                            name={'messageType'}
                                            key={messType.name}
                                            type={'radio'}
                                            component={formRadioComponent}
                                            label={messType.text}
                                            value={messType.name}
                                        />
                                    ))
                                }
                                <Field component={formInputComponent}
                                       label={'Можете указать свой контакт для ответа'}
                                       name={'contact'}
                                       placeholder={'Иван Иванович example@gmail.com'}
                                />
                                {
                                    props.messageType === 'review' &&
                                    <>
                                        <Form.Field>
                                            <label>Оцените сервис</label>
                                        </Form.Field>
                                        <Field
                                            name={'rating'}
                                            component={formRatingComponent}
                                        />
                                    </>
                                }

                                <Field
                                    name={'message'}
                                    component={formTextComponent}
                                    label={'Ваше сообщение'}
                                />
                                <Button onClick={handleSubmit} basic text={'Отправить'}/>
                            </Form>
                        </div>
                    </CustomModal>
            }
        </>
    )
};

const mapDispatchToProps = ({
    onSubmit: submitFeedback
});

const selector = formValueSelector('Feedback');

const mapStateToProps = state => {
    return {
        messageType: selector(state, 'messageType')
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(
    reduxForm(
        {
            form: 'Feedback',
            forceUnregisterOnUnmount: true
        }
    )(Feedback));
