import React from "react";
import {clearLocalStorage, loadState} from "./store/localStorage";
import {Button} from "./components/utils/UI";
import {clearAll} from "./store/workSpaceReducer";
import {connect} from "react-redux";

class ErrorHandler extends React.Component {
    constructor(props) {
        super(props);
        this.state = {hasError: false};
    }

    static getDerivedStateFromError(error) {
        // Обновить состояние с тем, чтобы следующий рендер показал запасной UI.

        return {hasError: true};
    }

    componentDidCatch(error, errorInfo) {
        // Можно также сохранить информацию об ошибке в соответствующую службу журнала ошибок
        console.log('FRONT ERROR has occurred!')
        // this.setState({hasError: true})
    }

    clickReturn() {
        this.setState({hasError: false});
        this.props.clearAll();
    }

    render() {
        if (this.state.hasError) {
            // Можно отрендерить запасной UI произвольного вида
            return <>
                <div style={{margin: '3rem'}}>
                    <h1>Что-то пошло не так!</h1>
                    <h2> Опишите пожалуйста сценарий, который привел к данной ошибке (что вы делали в этот момент) в
                        сообщении обратной связи (кнопка справа сверху на сайте), это поможет предотвратить появление
                        данной ошибки впредь.</h2>
                    <Button text={'Вернуться на сайт'} onClick={this.clickReturn.bind(this)}/>
                    <h3>Внимание! После нажатия на кнопку все данные на сайте будут сброшены. Это никак не повлияет на
                        ваш
                        аккаунт</h3>
                    <h3>Нужно будет заново зарегистрироваться или ввести все запросы по новой (если вы не
                        авторизованы)</h3>
                </div>
            </>;
        }

        return this.props.children;
    }
}

const mapStateToProps = (state) => {
    return {

    }
};

const mapDispatchToProps = ({
    clearAll
});

export default connect(mapStateToProps, mapDispatchToProps)(ErrorHandler);